<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-table :data="rightsList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="authName"></el-table-column>
        <el-table-column label="" prop="path"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.level === '0'"></el-tag>
            <el-tag type="success" v-else-if="scope.row.level === '1'"
              ></el-tag
            >
            <el-tag type="warning" v-else></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      rightsList: []
    }
  },
  created() {
    // 
    this.getRightsList()
  },
  methods: {
    async getRightsList() {
      const { data: res } = await this.$http.get('rights/list')
      if (res.meta.status !== 200) return this.$message.error('')
      this.rightsList = res.data
      console.log(this.rightsList);
    }
  }
}
</script>

<style lang="stylus" scoped></style>