<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addCateDialogVisible"
        width="50%"
        @close="addCateDialogClosed"
      >
        <!--  -->
        <el-form
          ref="addCateFormRef"
          :model="addCateForm"
          :rules="addCateFormRules"
          label-width="100px"
        >
          <el-form-item label="" prop="cat_name">
            <el-input v-model="addCateForm.cat_name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <!-- options  -->
            <!-- props  -->
            <el-cascader
              ref="refHandle"
              v-model="selectedKeys"
              :options="parentCateList"
              :props="cascaderProps"
              @change="parentCateChanged"
              clearable
            ></el-cascader>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addCateDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addCate"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <tree-table
        :data="catelist"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
        class="treeTable"
      >
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color: lightgreen"
          ></i>
          <i class="el-icon-error" v-else style="color: red"></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag
            type="success"
            size="mini"
            v-else-if="scope.row.cat_level === 1"
            ></el-tag
          >
          <el-tag type="warning" size="mini" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showEditDialog(scope.row.cat_id)"
            ></el-button
          >
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="removeRoleById(scope.row.cat_id)"
            ></el-button
          >
        </template>
      </tree-table>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClosed"
      >
        <el-form
          ref="editFormRef"
          :rules="addCateFormRules"
          :model="editForm"
          label-width="80px"
        >
          <!-- prop="roleName"  -->
          <el-form-item label="" prop="cat_name">
            <el-input v-model="editForm.cat_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editCateInfo"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="querInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="querInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      querInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      //   
      catelist: [],
      // 
      total: 0,
      //   table
      columns: [{
        label: '',
        prop: 'cat_name'
      }, {
        label: '',
        // 
        type: 'template',
        // 
        template: 'isok'
      }, {
        label: '',
        // 
        type: 'template',
        // 
        template: 'order'
      }, {
        label: '',
        // 
        type: 'template',
        // 
        template: 'opt'
      }],
      //   
      addCateDialogVisible: false,
      // 
      addCateForm: {
        // 
        cat_name: '',
        // Id
        cat_pid: 0,
        // 1
        cat_level: 0
      },
      //   
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //   
      parentCateList: [],
      //   
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: 'true'
      },
      //   Id
      selectedKeys: [],
      // 
      editDialogVisible: false,
      // 
      editForm: {},
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    //   
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.querInfo
      })

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      //   console.log(res.data);
      //    catelist
      this.catelist = res.data.result
      //   
      this.total = res.data.total
    },
    //  pagesize 
    handleSizeChange(newSize) {
      this.querInfo.pagesize = newSize
      this.getCateList()
    },
    //  pagenum 
    handleCurrentChange(newPage) {
      this.querInfo.pagenum = newPage
      this.getCateList()
    },
    // 
    showAddCateDialog() {
      // 
      this.getParentCateList()
      // 
      this.addCateDialogVisible = true
    },
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: {
          type: 2
        }
      })

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      // console.log(res.data);
      this.parentCateList = res.data
    },
    // 
    parentCateChanged() {
      // console.log(this.selectedKeys);
      //  this.selectedKeys  length 0
      // 
      if (this.selectedKeys.length) {
        // Id
        this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        // 
        this.addCateForm.cat_level = this.selectedKeys.length
      } else {
        // Id
        this.addCateForm.cat_pid = 0
        // 
        this.addCateForm.cat_level = 0
      }
      // 
      this.$refs.refHandle.dropDownVisible = false;
    },
    // 
    addCate() {
      // console.log(this.addCateForm);
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('categories', this.addCateForm)

        if (res.meta.status !== 201) {
          return this.$message.error('')
        }

        this.$message.success('')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    },
    // 
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    },
    // 
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('categories/' + id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)

      this.editForm = res.data
      // console.log(this.editForm);
      this.editDialogVisible = true
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editCateInfo() {
      this.$refs.editFormRef.validate(async valid => {
        // console.log(valid);
        if (!valid) return
        // 
        const { data: res } = await this.$http.put('categories/' + this.editForm.cat_id, this.editForm)

        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)

        // 
        this.editDialogVisible = false
        // 
        this.getCateList()
        // 
        this.$message.success('')
      })
    },
    // Id
    async removeRoleById(id) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      //  confirm
      //  cancel
      // console.log(confirmResult);
      if (confirmResult !== 'confirm') return this.$message.info('')

      const { data: res } = await this.$http.delete('categories/' + id)

      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)

      this.$message.success('')
      this.getCateList()
    },
  }
}
</script>

<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;
}
</style>