<template>
  <el-container class="home-container">
    <!--  -->
    <el-header>
      <div>
        <img src="../assets/heima.png" alt="" />
        <span></span>
      </div>
      <el-button type="info" @click="logout"></el-button>
    </el-header>
    <!--  -->
    <el-container>
      <!--  -->
      <el-aside :width="isCollapse ? '66px' : '200px'">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <!--  -->
        <el-menu
          background-color="#333744"
          text-color="#fff"
          active-text-color="#3b9bff"
          unique-opened
          :collapse="isCollapse"
          :collapse-transition="false"
          router
          :default-active="activePath"
        >
          <!--  -->
          <!-- :index   ''  -->
          <el-submenu
            :index="item.id + ''"
            v-for="item in menulist"
            :key="item.id"
          >
            <!--  -->
            <template slot="title">
              <!--  -->
              <i :class="item.icon"></i>
              <!--  -->
              <span>{{ item.authName }}</span>
            </template>
            <!--  -->
            <el-menu-item
              :index="'/' + subItem.path"
              v-for="subItem in item.children"
              :key="subItem.id"
              @click="saveNavState('/' + subItem.path)"
            >
              <template slot="title">
                <!--  -->
                <i class="el-icon-menu"></i>
                <!--  -->
                <span>{{ subItem.authName }}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!--  -->
      <el-main>
        <!--  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      // 
      menulist: [],
      iconsObj: ['icon-user', 'icon-tijikongjian', 'icon-shangpin', 'icon-danju', 'icon-baobiao'],
      // 
      isCollapse: false,
      // 
      activePath: ''
    }
  },
  created() {
    this.getMenuList()
    this.activePath = window.sessionStorage.getItem('activePath')
  },
  methods: {
    logout() {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    // 
    async getMenuList() {
      const { data: res } = await this.$http.get('menus')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.menulist = res.data
      // console.log(res);

      //  icon 
      this.menulist.forEach((value, index) => {
        value.icon = `iconfont ${this.iconsObj[index]}`
        // console.log(value);
      })
    },
    // 
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    },
    // 
    saveNavState(activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
}

.el-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 0;
  color: #fff;
  font-size: 20px;
  background-color: #373d41;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
}

.el-aside {
  background-color: #333744;
  .el-menu {
    border-right: none;
  }
}

.el-main {
  background-color: #eaedf1;
}

.iconfont {
  margin-right: 5px;
}

.toggle-button {
  background-color: #485164;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
</style>